		transition: width 0.2s;
		display: none;
	}
	.wx-event-calendar-calendars_wrapper {
		margin-top: 10px;
	}

	.wx-event-calendar-show {
		border-right: var(--wx-border);
		width: 249px;
		display: flex;
	}
